"use client";

import { motion, useScroll, useTransform } from "motion/react";
import Image from "next/image";
import Link from "next/link";
import { useRef } from "react";

const processSteps = [
  { number: "01", title: "Sketch Design" },
  { number: "02", title: "Design Development" },
  { number: "03", title: "Development Application" },
  { number: "04", title: "Interior Design" },
  { number: "05", title: "Building approval plans + documentation" },
  { number: "06", title: "Construction plans + documentation" }
];

export default function Process() {
  const imageRef = useRef<HTMLDivElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);

  const { scrollYProgress } = useScroll({
    target: imageRef,
    offset: ["end end", "start start"]
  });

  const scale = useTransform(scrollYProgress, [0, 1], [1.1, 1]);

  return (
    <section
      ref={containerRef}
      className="relative z-20 px-4 lg:px-8 py-24 bg-white"
    >
      <div className="max-w-8xl mx-auto">
        {/* Header */}
        <div className="mb-12">
          <motion.p
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
            className="text-lg font-semibold uppercase tracking-wider mb-4 text-black"
          >
            (OUR PROCESS)
          </motion.p>
        </div>

        {/* Main Content Flex Layout */}
        <div className="flex flex-col lg:flex-row gap-12 lg:gap-44">
          {/* Left Side - Image and Process Steps (30% width) */}
          <div className="w-[470px] flex-shrink-0">
            {/* Larger Image */}
            <motion.div
              ref={imageRef}
              initial={{ opacity: 0, x: -50 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.8, delay: 0.2 }}
              className="mb-12"
            >
              <div className="relative overflow-hidden rounded-lg aspect-[5/5] w-full">
                <motion.div style={{ scale }} className="w-full h-full">
                  <Image
                    src="/img/pro.avif"
                    alt="OH Architecture building exterior"
                    fill
                    className="object-cover"
                    priority
                  />
                </motion.div>
              </div>
            </motion.div>

            {/* Process Steps List */}
            <div className="space-y-6">
              {processSteps.map((step, index) => (
                <motion.div
                  key={index}
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.6, delay: 0.4 + index * 0.1 }}
                >
                  <div className="flex items-start gap-3 text-lg">
                    <span className="text-black font-medium">({step.number})</span>
                    <span className="text-black font-semibold flex-1">{step.title}</span>
                  </div>
                  {index < processSteps.length - 1 && (
                    <div className="w-full h-px bg-gray-300 mt-4"></div>
                  )}
                </motion.div>
              ))}
            </div>
          </div>

          {/* Right Side - Content (70% width) */}
          <div className="lg:w-full flex flex-col justify-start">
            <motion.div
              initial={{ opacity: 0, x: 50 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 0.8, delay: 0.4 }}
              className="space-y-10"
            >
              {/* Main Heading */}
              <h2 className="text-[clamp(38px,5vw,76px)] font-semibold leading-[1] text-black indent-12">
                Our approach at OH Architecture is designed to make your journey
                from concept to completion as smooth and enjoyable as possible.
              </h2>

              {/* Subtext */}
              <p className="text-[clamp(38px,5vw,76px)] font-semibold leading-[1] text-black ">
                With our 6-stage process, we prioritise clarity, collaboration,
                and your unique vision. At every step, we'll keep you informed,
                inspired, and involved.
              </p>
            </motion.div>

            {/* CTA Button */}
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 0.8 }}
              className="pt-12"
            >
              <Link
                href="/process"
                className="inline-flex items-center gap-2 bg-black text-white px-10 py-5 rounded-full font-bold text-sm uppercase tracking-wider hover:bg-neutral-800 transition-colors duration-300"
              >
                GET TO KNOW OUR PROCESS
                <svg
                  className="w-5 h-5"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M9 5l7 7-7 7"
                  />
                </svg>
              </Link>
            </motion.div>
          </div>
        </div>
      </div>
    </section>
  );
}
