"use client";

import { motion, useScroll, useTransform, useSpring } from "motion/react";
import Image from "next/image";
import Link from "next/link";
import { useRef, useEffect, useState } from "react";
import { Project, projects } from "@/data/projects";

// Work Section Component (for homepage)
export default function WorkSection() {
  const containerRef = useRef<HTMLDivElement>(null);
  const [isMobile, setIsMobile] = useState(false);
  
  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 768);
    };
    
    checkMobile();
    window.addEventListener('resize', checkMobile);
    
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  if (isMobile) {
    return <MobileWorkSection />;
  }

  return <DesktopWorkSection containerRef={containerRef} />;
}

// Desktop Work Section with horizontal scroll
function DesktopWorkSection({ containerRef }: { containerRef: React.RefObject<HTMLDivElement> }) {
  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start start", "end end"]
  });
  
  const scaleProgress = useSpring(scrollYProgress, {
    stiffness: 100,
    damping: 30,
    restDelta: 0.001
  });
  
  const scrollProgress = useTransform(scaleProgress, [0, 1], [0, 100]);

  return (
    <section className="relative h-screen overflow-hidden bg-white">
      {/* Fixed footer elements */}
      <div className="fixed bottom-8 left-8 z-30">
        <motion.h2 
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1 }}
          className="text-7xl font-bold uppercase"
        >
          Works
        </motion.h2>
      </div>
      
      <div className="fixed bottom-8 left-1/2 transform -translate-x-1/2 z-30 flex items-center gap-2">
        <motion.span
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1, delay: 0.3 }}
          className="text-sm uppercase tracking-widest"
        >
          Scroll to explore
        </motion.span>
        <motion.span
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 1, delay: 0.5 }}
          className="text-sm font-mono"
        >
          ({Math.round(scrollProgress.get())}%)
        </motion.span>
      </div>
      
      <div className="fixed bottom-8 right-8 z-30 flex gap-6">
        <motion.a
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.7 }}
          href="#"
          className="text-sm uppercase tracking-widest hover:underline"
        >
          In progress ↗
        </motion.a>
        <motion.a
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.9 }}
          href="#"
          className="text-sm uppercase tracking-widest hover:underline"
        >
          Archive ↗
        </motion.a>
      </div>

      {/* Horizontal scroll container */}
      <div 
        ref={containerRef} 
        className="h-full overflow-x-auto overflow-y-hidden flex snap-x snap-mandatory"
        style={{ scrollbarWidth: 'none' }}
      >
        <div className="flex h-full">
          {projects.map((project, index) => (
            <ProjectSlide 
              key={project.slug} 
              project={project} 
              index={index} 
              total={projects.length} 
            />
          ))}
        </div>
      </div>
    </section>
  );
}

// Project Slide Component
function ProjectSlide({ project, index, total }: { project: Project; index: number; total: number }) {
  const slideRef = useRef<HTMLDivElement>(null);
  
  return (
    <div 
      ref={slideRef}
      className="h-full w-screen flex-shrink-0 snap-start flex items-center justify-center px-16"
    >
      <Link href={`/work/${project.slug}`} className="block h-4/5 w-full relative group">
        <motion.div 
          className={`relative h-full w-full overflow-hidden rounded-lg ${
            index % 2 === 0 ? 'aspect-video' : 'aspect-[9/16]'
          }`}
          whileHover="hover"
          initial={{ opacity: 0, scale: 0.9 }}
          whileInView={{ opacity: 1, scale: 1 }}
          transition={{ duration: 0.7 }}
        >
          <Image
            src={project.backgroundImageUrl}
            alt={project.title}
            fill
            className="object-cover"
          />
          
          <motion.div 
            className="absolute inset-0 bg-black/30 opacity-0 group-hover:opacity-100 transition-opacity duration-500"
            variants={{
              hover: { opacity: 1 }
            }}
          />
          
          <div className="absolute bottom-6 left-6 text-white z-10">
            <motion.span 
              className="block text-sm mb-1"
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.2 }}
            >
              ({String(index + 1).padStart(2, '0')}/{String(total).padStart(2, '0')})
            </motion.span>
            <motion.h3 
              className="text-3xl font-medium"
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.3 }}
            >
              {project.title}
            </motion.h3>
            <motion.span 
              className="block text-sm mt-1"
              initial={{ opacity: 0, y: 20 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ delay: 0.4 }}
            >
              {project.year}
            </motion.span>
          </div>
        </motion.div>
      </Link>
    </div>
  );
}

// Mobile Work Section with vertical scroll
function MobileWorkSection() {
  return (
    <section className="min-h-screen py-12 px-4 bg-white">
      <motion.h2 
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="text-5xl font-bold uppercase mb-12"
      >
        Works
      </motion.h2>
      
      <div className="grid grid-cols-1 gap-8">
        {projects.map((project, index) => (
          <Link key={project.slug} href={`/work/${project.slug}`}>
            <motion.div 
              className="relative h-80 w-full overflow-hidden rounded-lg"
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.1 }}
            >
              <Image
                src={project.backgroundImageUrl}
                alt={project.title}
                fill
                className="object-cover"
              />
              
              <div className="absolute inset-0 bg-black/20 opacity-0 hover:opacity-100 transition-opacity duration-300" />
              
              <div className="absolute bottom-4 left-4 text-white">
                <span className="block text-sm mb-1">
                  ({String(index + 1).padStart(2, '0')})
                </span>
                <h3 className="text-xl font-medium">{project.title}</h3>
                <span className="block text-xs mt-1">{project.year}</span>
              </div>
            </motion.div>
          </Link>
        ))}
      </div>
      
      <div className="flex justify-center gap-6 mt-12">
        <a href="#" className="text-sm uppercase tracking-widest hover:underline">
          In progress ↗
        </a>
        <a href="#" className="text-sm uppercase tracking-widest hover:underline">
          Archive ↗
        </a>
      </div>
    </section>
  );
}